<template>
  <div class="header clearfix">
    <!--背景层-->
    <div class="headbg">
      <img :src="seller.avatar">
    </div>
    <!--内容区-->
    <div class="top clearfix">
      <div class="left">
        <img :src="seller.avatar" alt="">
      </div>
      <div class="right">
        <h2><img src="./images/brand@2x.png" alt="">{{seller.name}}</h2>
        <p class="des">{{seller.description}}/{{seller.deliveryTime}}分钟到达</p>
        <p class="sup_des">
          <!--<images :src="supportPic[seller.supports[0].type]">-->
          <span class="icon" :class="supportPic[seller.supports&&seller.supports[0].type]"></span>
          <span>{{seller.supports&&seller.supports[0].description}}</span>
        </p>
      </div>
      <div class="more" @click="flag=true">
        {{seller.supports&&seller.supports.length}}个
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <!--公告栏-->
    <div class="bulletin" @click="flag=true">
      <img src="./images/bulletin@2x.png" alt="">
      <p>{{seller.bulletin}}</p>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <!--蒙版页-->
    <transition name="slide-left">
    <div class="moreInfo" v-show="flag">
      <div class="main">
        <h2>{{seller.name}}</h2>
        <div class="score">
          <!--星级-->
          <star :score="seller.score"></star>
        </div>
        <div class="title">
          <div class="line"></div>
          <div class="text">优惠信息</div>
          <div class="line"></div>
        </div>
        <ul class="supports">
          <li v-for="support in seller.supports">
            <span class="icon" :class="supportPic[support.type]"></span>
            <span class="text">{{support.description}}</span>
          </li>
        </ul>
        <div class="title">
          <div class="line"></div>
          <div class="text">商家公告</div>
          <div class="line"></div>
        </div>
        <div class="bulletin">
          {{seller.bulletin}}
        </div>

      </div>
      <div class="close">
        <span class="icon-close" @click="flag=false"></span>
      </div>
    </div>
    </transition>
  </div>
</template>

<script>
  import axios from 'axios';
  import Star from '../stars/Star.vue';
  export default {
    components:{
      Star
    },
    data() {
      return {
        seller: {},
        supportPic:[
          'decrease',
          'discount',
          'guarantee',
          'invoice',
          'special'
        ],
        flag:false
      }
    },
    created() {
      let that = this;
      axios.get('/api/seller')
        .then((res) => {
//          console.log(res);
          that.seller = res.data.data;
        }).catch((err) => {
        console.log(err);
      });
    }

  }
</script>

<style>
  .header {
    overflow: hidden;/*解决margin塌陷*/
    background: rgba(7,17,27,0.5);
  }
  .headbg{
    width: 100%;
    height: 134px;
    position: absolute;
    top:0;
    left:0;
    filter:blur(10px);
    z-index: -1;
  }
  .headbg img{
    width: 100%;
    height: 100%;
  }
  .top {
    margin: 24px 0 18px 24px;
    position: relative;

  }
  .top .left {
    width: 64px;
    height: 64px;
    margin-right: 16px;
    float: left;
  }
  .top .left img {
    width: 100%;
    border-radius: 4px;
  }
  .top .right {
    float: left;
    padding: 2px 0px;
  }
  .top .right h2 {
    font-size: 16px;
    color: #fff;
    font-weight: bold;
    line-height: 18px;
  }
  .top .right h2 img {
    width: 30px;
    margin-right: 6px;
    float: left;
  }
  .top .right .des {
    margin: 8px 0px 10px;
    font-size: 12px;
    color: #fff;
    font-weight: 200;
    line-height: 12px;
  }
  .top .right .sup_des {
    font-size: 10px;
    font-weight: 200;
    color: #fff;
    line-height: 12px;
  }
  .top .right .sup_des .icon {
    width: 12px;
    height: 12px;
    display:inline-block;
    float: left;
    margin-right: 4px;
    background-size: 12px;
  }
  .top .right .sup_des .icon.decrease {
    background-image: url('images/decrease_1@2x.png');
  }
  .top .right .sup_des .icon.discount {
    background-image: url('images/discount_1@2x.png');
  }
  .top .right .sup_des .icon.guarantee {
    background-image: url('images/guarantee_1@2x.png');
  }
  .top .right .sup_des .icon.invoice {
    background-image: url('images/invoice_1@2x.png');
  }
  .top .right .sup_des .icon.special {
    background-image: url('images/special_1@2x.png');
  }
  .bulletin {
    height: 28px;
    padding: 0 12px;
    position: relative;
  }
  .bulletin p {
    font-size: 10px;
    color: #fff;
    font-weight: 200;
    line-height: 28px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-right:8px;
  }
  .bulletin img {
    width: 22px;
    float: left;
    margin: 8px 4px 0 0;

  }
  .bulletin .icon-keyboard_arrow_right{
    color: #fff;
    position: absolute;
    right: 10px;
    top: 6px;
  }
  .top .more{
    position: absolute;
    bottom: -4px;
    right: 12px;
    font-size: 10px;
    color: #fff;
    line-height: 12px;
    padding:7px 8px;
    background: rgba(0,0,0,0.2);
    border-radius: 12px;
  }
  .top .more .icon-keyboard_arrow_right{
    float: right;
    margin-left: 2px;
  }
  .moreInfo{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0px;
    left: 0px;
    overflow: auto;
    box-sizing: border-box;
    background: rgba(7,17,27,0.8);
    padding: 64px 36px 32px;
    z-index: 99;
  }
  .moreInfo .main{
    min-height: 94%;
    width: 100%;
    padding-bottom: 64px;
  }
  .moreInfo .main h2{
    font-size: 16px;
    font-weight: 700;
    color: #fff;
    line-height: 16px;
    text-align: center;
  }
  .moreInfo .main .score{
    height: 24px;
    width: 80%;
    margin: 16px auto;
  }
  .moreInfo .main .title{
    margin: 28px 0px 24px;
    display: flex;
  }
  .moreInfo .main .title .line{
    flex:1;
    width: 112px;
    border-top: 1px solid rgba(255,255,255,0.2);
    position: relative;
    top: 6px;
  }
  .moreInfo .main .title .text{
    line-height: 14px;
    font-size: 14px;
    color: #fff;
    text-align: center;
    font-weight: 700;
    margin:0 12px;
  }
  .moreInfo .main ul.supports{
    padding:0 12px;
  }
  .moreInfo .main ul.supports li{
    margin-top: 12px;
  }
  .moreInfo .main ul.supports li:first-child{
    margin-top: 0;
  }
  .moreInfo .main ul.supports .icon{
    width: 16px;
    height: 16px;
    display: inline-block;
    float: left;
    background-size: 16px;
    margin-right: 6px;
  }
  .moreInfo .main ul.supports .icon.decrease {
    background-image: url('images/decrease_1@3x.png');
  }
  .moreInfo .main ul.supports .icon.discount {
    background-image: url('images/discount_1@3x.png');
  }
  .moreInfo .main ul.supports .icon.guarantee {
    background-image: url('images/guarantee_1@3x.png');
  }
  .moreInfo .main ul.supports .icon.invoice {
    background-image: url('images/invoice_1@3x.png');
  }
  .moreInfo .main ul.supports .icon.special {
    background-image: url('images/special_1@3x.png');
  }
  .moreInfo .main ul.supports .text{
    font-size: 12px;
    color: #fff;
    line-height: 12px;
    font-weight: 200;
  }
  .moreInfo .main .bulletin{
    padding:0 12px;
    font-size: 12px;
    color: #fff;
    line-height: 24px;
  }
  .moreInfo .close{
    text-align: center;
    margin-top: -64px;
  }
  .moreInfo .close .icon-close{
    font-size: 32px;
    color: rgba(255,255,255,0.5);
  }
  .slide-left-enter-active, .slide-left-leave-active{
    transition: all 0.5s;
  }
  .slide-left-enter, .slide-left-leave-to{
    transform:translateX(100%);
  }
  .fade-enter-active, .fade-leave-active{
    transition: all 0.5s;
  }
  .fade-enter, .fade-leave-to{
    opacity:0;
  }
</style>



