<template>
  <transition name="fade">
  <div class="goods" v-if="goods!=null">
    <div class="menu" ref="menu">
      <ul>
        <li class="menu_item" v-for="(item,index) in goods" :class="{active:menuIndex==index}">
          <div class="con">
            <span class="icon" v-if="item.type>=0" :class="supportPic[item.type]"></span>
            <span class="text">{{item.name}}</span>
          </div>
        </li>
      </ul>
    </div>
    <div class="goodsWrapper" id="goodsWrapper" ref="goodsWrapper">
      <ul>
        <li class="con_item" v-for="good in goods">
          <h2>{{good.name}}</h2>
          <ul>
            <li v-for="food in good.foods">
              <div class="icon" @click="detailFood=food">
                <img :src="food.icon" width="57" height="57">
              </div>
              <div class="info">
                <p class="name">{{food.name}}</p>
                <p class="des">{{food.description}}</p>
                <p class="sellInfo">
                  <span class="count">月售{{food.sellCount}}份</span>
                  <span class="rating">好评率{{food.rating}}%</span>
                </p>
                <p class="price">
                  <span class="new"><span>&yen;</span>{{food.price}}</span>
                  <span class="old" v-if="food.oldPrice"><span>&yen;</span>{{food.oldPrice}}</span>
                </p>
                <div class="num_control">
                  <!--数量加减模块-->
                  <numcontrol :food="food"></numcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <!--底部购物车组件-->
    <shopcart :selectFoods="selectFoods"></shopcart>
    <!--详情页-->
    <my-detail :food="detailFood" v-if="detailFood" @close="closeDetails"></my-detail>
  </div>
  </transition>
</template>

<script>
  import axios from 'axios';
  import Scroll from 'better-scroll';
  import Shopcart from '../Shopcart.vue';
  import Numcontrol from '../Numcontrol.vue';
  import Details from '../details/Details.vue';

  export default {
    components:{
      Shopcart,
      Numcontrol,
      myDetail:Details
    },
    data() {
      return {
        goods: null,
        supportPic: [],
        menuScroll: null,
        goodsScroll: null,
        scrollY: 0,
        itemHeight: [],   //保存各项的高度
        detailFood:null   //保存正在展示详情的food
      }
    },
    created() {
      this.supportPic = ['decrease', 'discount', 'guarantee', 'invoice', 'special'];
      let that = this;
      axios.get('/api/goods')
        .then((res) => {
//          console.log(res);
          that.goods = res.data.data;
          that.$nextTick(() => { //防止内容还未加载完就执行，获取不到元素的高度导致无法滑动
            that._initScroll(); //启用滚动
            that.goodsScroll.on("scroll", (pos) => {
              that.scrollY = Math.abs(Math.round(pos.y));
            });
            that.calHeight();
          });
        }).catch((err) => {
        console.log(err);
      });
    },
    methods: {
      _initScroll() {
        this.menuScroll = new Scroll(this.$refs.menu, {});
        this.goodsScroll = new Scroll(this.$refs.goodsWrapper, {
          click: true,
          probeType: 3
        });
      },
      calHeight() {
        let lis = document.getElementById('goodsWrapper').getElementsByClassName('con_item');
        let height = 0;
        this.itemHeight.push(height);
        //计算每一项到顶部的高度，保存到数组中
        for (let i = 0; i < lis.length; i++) {
          height += lis[i].clientHeight;
          this.itemHeight.push(height);
        }
      },
      closeDetails(res){
        this.detailFood=res;
      }
    },
    computed: {
      menuIndex() {
        for (let i = 0; i < this.itemHeight.length; i++) {
          let h1 = this.itemHeight[i];
          let h2 = this.itemHeight[i + 1];
          if (this.scrollY > h1 && this.scrollY < h2) {
            return i;
          }
        }
        return 0;
      },
      selectFoods(){
        let foodslist=[];
        this.goods.forEach(good=>{
          good.foods.forEach(food=>{
            if(food.count){
              foodslist.push(food);
            }
          });
        });
        return foodslist;
      }
    }
  }
</script>

<style>
  .goods {
    display: flex;
    position: absolute;
    width: 100%;
    background: #ffffff;
    top: 175px;
    bottom: 46px;
    overflow: hidden;
  }
  .menu {
    flex: 0 0 80px;
  }
  .menu ul {
    width: 100%;
    background: #f3f5f7;
  }
  .menu ul li.menu_item {
    width: 100%;
    height: 54px;
    padding: 0 12px;
    display: table;
    box-sizing: border-box;
  }
  .menu ul li.menu_item.active {
    background: #fff;
  }
  .menu ul li.menu_item .con {
    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
    display: table-cell;
    vertical-align: middle;
  }
  .menu ul li.menu_item.active .con{
    border:none;
  }
  .menu ul li.menu_item .con .icon {
    width: 12px;
    height: 12px;
    display: inline-block;
    float: left;
    margin: 3px 3px 0 0;
    background-size: 12px;
  }
  .menu ul li.menu_item .con .icon.decrease {
    background-image: url('images/decrease_3@2x.png');
  }
  .menu ul li.menu_item .con .icon.discount {
    background-image: url('images/discount_3@2x.png');
  }
  .menu ul li.menu_item .con .icon.guarantee {
    background-image: url('images/guarantee_3@2x.png');
  }
  .menu ul li.menu_item .con .icon.invoice {
    background-image: url('images/invoice_3@2x.png');
  }
  .menu ul li.menu_item .con .icon.special {
    background-image: url('images/special_3@2x.png');
  }
  .menu ul li.menu_item .con .text {
    font-size: 12px;
    font-weight: 200;
    color: #000;
  }
  .menu ul li.menu_item.active .con .text {
    color: rgb(7, 17, 27);
    font-weight: 600;
  }
  .goodsWrapper {
    flex: 1;
  }
  .goodsWrapper ul li.con_item {
  }
  .goodsWrapper ul li.con_item h2 {
    height: 26px;
    background: #f3f5f7;
    border-left: 2px solid #d9dde1;
    padding-left: 14px;
    font-size: 12px;
    line-height: 26px;
    color: rgb(147, 153, 159);
  }
  .goodsWrapper ul li.con_item ul {
    padding: 18px;
  }
  .goodsWrapper ul li.con_item ul li {
    display: flex;
    margin-bottom: 9px;
    padding-bottom: 9px;
    border-bottom: 1px solid rgba(7, 17, 27, 0.1);
    position: relative;/*给增减模块定位*/
  }
  .goodsWrapper ul li.con_item ul li:last-child {
    margin-bottom: 0;
  }
  .goodsWrapper ul li.con_item ul li:last-child {
    border: none;
  }
  .con_item ul li .icon {
    flex: 0 0 57px;
  }
  .con_item ul li .info {
    flex: 1;
    margin: 2px 0 0 10px;
  }
  .con_item ul li .info .name {
    font-size: 14px;
    color: rgb(7, 17, 27);
    line-height: 14px;
  }
  .con_item ul li .info .des {
    margin-top: 8px;
    font-size: 10px;
    color: rgb(147, 153, 159);
    line-height: 12px;
  }
  .con_item ul li .info .sellInfo {
    margin-top: 8px;
    font-size: 10px;
  }
  .con_item ul li .info .sellInfo .count {
    margin-right: 12px;
    color: rgb(147, 153, 159);
  }
  .con_item ul li .info .sellInfo .rating {
    color: rgb(147, 153, 159);
  }
  .con_item ul li .info .price {
    margin-top: 8px;
  }
  .con_item ul li .info .price .new {
    font-size: 14px;
    color: rgb(240, 20, 20);
    font-weight: 700;
    line-height: 24px;
  }
  .con_item ul li .info .price .new span {
    font-size: 10px;
    color: rgb(240, 20, 20);
  }
  .con_item ul li .info .price .old {
    font-size: 10px;
    color: rgb(147, 153, 159);
    font-weight: 700;
    line-height: 24px;
    text-decoration: line-through;
  }
  .con_item ul li .info .price .old span {
    font-weight: normal;
    color: rgb(147, 153, 159);

  }
  .con_item ul li .num_control{
    width: 60px;
    height: 20px;
    position: absolute;
    right: 9px;
    bottom: 12px;
  }

</style>



