<template>
    <div class="shop_cart">
      <div class="cart" @click="flag=!flag">
        <div class="icon">
          <span :class="{active:totalCount}"><i class="icon-shopping_cart"></i></span>
        </div>
        <div class="price">
          <span class="num" :class="{active:totalPrice}">￥{{totalPrice}}</span>
        </div>
        <div class="info">另需配送费￥4元</div>
        <div class="icon_num" v-show="totalCount">{{totalCount}}</div>
      </div>
      <div class="btn">
        <span v-if="!totalPrice">￥20起送</span>
        <span v-else-if="totalPrice<20">还差￥{{20-totalPrice}}起送</span>
        <span v-else="" class="submit" @click="aaa">结算</span>
      </div>
      <transition name="slide-top">
      <div class="background" v-show="flag"></div>
      </transition>
      <transition name="slide-top">
      <div class="food_list" v-show="flag">
        <div class="head">
          <span class="title">购物车</span>
          <span class="empty" @click="doEmpty">清空</span>
        </div>
        <ul>
          <li v-for="food in selectFoods">
            <div class="name">{{food.name}}</div>
            <div class="right">
              <span class="yuan">￥</span>
              <span class="price">{{food.price}}</span>
              <div class="num_control">
               <num-control :food="food"></num-control>
              </div>
            </div>
          </li>
        </ul>
      </div>
      </transition>
    </div>
</template>

<script>
  import Numcontrol from './Numcontrol.vue';
  export default {
    props:['selectFoods'],
    data(){
      return{
        price:0,
        flag:false
      }
    },
    components:{
      numControl:Numcontrol
    },
    methods:{
      doEmpty(){
        this.selectFoods.forEach(food=>{
          food.count=0;
        });
        this.flag=false;
      },
      aaa(){
        console.log('提交成功');
      }
    },
    computed:{
      totalCount(){
        let count=0;
        for(let i=0;i<this.selectFoods.length;i++){
          count+=this.selectFoods[i].count;
        }
        return count;
      },
      totalPrice(){
        let price=0;
        for(let i=0;i<this.selectFoods.length;i++){
          price+=this.selectFoods[i].price*this.selectFoods[i].count;
        }
        return price;
      }
    }
  }
</script>

<style>
  .shop_cart{
    width: 100%;
    height: 46px;
    background: #141d27;
    position: fixed;
    left:0;
    bottom: 0;
    display: flex;
    z-index: 9;
  }
  .shop_cart .cart{
    flex:1;
    position: relative;
  }
  .shop_cart .cart .icon{
    width: 56px;
    height: 56px;
    background: #141d27;
    position: absolute;
    left: 12px;
    bottom: 2px;
    border-radius: 50%;
    padding: 6px;
    box-sizing: border-box;
    z-index: 10;
  }
  .shop_cart .cart .icon span{
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 50%;
    background: rgba(255,255,255,0.2);
    text-align: center;
    line-height: 50px;
    font-size: 24px;
    color: rgba(255,255,255,0.4);
  }
  .shop_cart .cart .icon span.active{
    background:rgb(0,160,220) ;
    color: #fff;
  }
  .shop_cart .cart .price{
    height: 100%;
    float: left;
    box-sizing: border-box;
    margin-left: 68px;
    padding:12px;
    font-size: 16px;
    line-height: 24px;
  }
  .shop_cart .cart .price .num{
    color: rgba(255,255,255,0.4);
    font-weight: 700;
  }
  .shop_cart .cart .price .num.active{
    color: #fff;
  }
  .shop_cart .cart .info{
    float: left;
    border-left:1px solid rgba(255,255,255,0.1);
    padding-left: 12px;
    margin: 12px 0;
    font-size: 12px;
    font-weight: normal;
    color:rgba(255,255,255,0.4);
    line-height: 24px;
  }
  .shop_cart .cart .icon_num{
    width: 24px;
    line-height: 16px;
    background: rgb(240,20,20);
    border-radius: 6px;
    font-size: 9px;
    color: #fff;
    text-align: center;
    font-weight: 700;
    position: absolute;
    left: 44px;
    top: -10px;
    z-index: 10;
  }
  .shop_cart .btn{
    flex:0 0 105px;

  }
  .shop_cart .btn span{
    width: 100%;
    height: 100%;
    background: rgba(255,255,255,0.2);
    padding: 12px 8px;
    box-sizing: border-box;
    display: block;
    font-size: 12px;
    color: rgba(255,255,255,0.4);
    font-weight: 700;
    line-height: 24px;
    text-align: center;
  }
  .shop_cart .btn span.submit{
    background: rgb(240,20,20);
    color: #fff;
    font-size: 14px;
  }
  .shop_cart .background{
    width: 100%;
    height: 100%;
    background: rgba(7,17,27,0.6);
    position: fixed;
    top:0;
    left:0;
    z-index: -1;
  }
  .shop_cart .food_list{
    width: 100%;
    background: white;
    position: absolute;
    bottom: 46px;
    left: 0;
    z-index: 9;
  }
  .shop_cart .food_list .head{
    width: 100%;
    height: 40px;
    background: #f3f5f7;
    padding:0 18px;
    line-height: 40px;
    box-sizing: border-box;
    border-bottom: 1px solid rgba(7,17,27,0.1);
  }
  .shop_cart .food_list .head .title{
    font-size: 14px;
    color: rgb(7,17,27);
  }
  .shop_cart .food_list .head .empty{
    font-size: 12px;
    color: rgb(0,160,220);
    float: right;
  }
  .shop_cart .food_list ul{
    box-sizing: border-box;
    padding:0 18px 24px;
  }
  .shop_cart .food_list ul li{
    width: 100%;
    height: 48px;
    padding: 12px 0;
    box-sizing: border-box;
    border-bottom: 1px solid rgba(7,17,27,0.1);
    line-height: 24px;
  }
  .shop_cart .food_list ul li .name{
    float: left;
    font-size: 14px;
    color: rgb(7,17,27);
  }
  .shop_cart .food_list ul li .right{
    float: right;
  }
  .shop_cart .food_list ul li .right .yuan{
    font-size: 10px;
    color: rgb(240,20,20);
    float: left;
    margin-left: 18px;
  }
  .shop_cart .food_list ul li .right .price{
    font-size: 14px;
    font-weight: 700;
    color: rgb(240,20,20);
    float: left;
    margin-right: 12px;
  }
  .shop_cart .food_list ul li .right .num_control{
    float: right;
  }
  .slide-top-enter-active, .slide-top-leave-active{
    transition: all 0.5s;
  }
  .slide-top-enter, .slide-top-leave-to{
    transform:translateY(100%);
    opacity:0;
  }
</style>



