<template>
  <div class="details" ref="details_scroll">
    <div>
      <div class="bigpic">
        <img :src="food.image" alt="">
      </div>
      <div class="headinfo">
        <div class="name">{{food.name}}</div>
        <div class="sell_info">
          <span>月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
        </div>
        <div class="price">
          <span class="new"><span>￥</span>{{food.price}}</span>
          <span class="old" v-if="food.oldPrice"><span>￥</span>{{food.oldPrice}}</span>
        </div>
        <div class="addcart" @click="addCart">加入购物车</div>
      </div>
      <div class="desprition">
        <h4>商品介绍</h4>
        <p>{{food.info}}</p>
      </div>
      <div class="details_ratings">
        <h4>商品评价</h4>
        <div class="ratings_cls clearfix">
          <div class="all" :class="{active:type==2}" @click="type=2">全部 <span>{{food.ratings.length}}</span></div>
          <div class="good" :class="{active:type==0}" @click="type=0">满意 <span>{{goodnum}}</span></div>
          <div class="bad" :class="{active:type==1}" @click="type=1">不满意 <span>{{badnum}}</span></div>
        </div>
        <div class="btn" @click="changeShow" :class="{active:!showAll}">
          <i class="icon-check_circle"></i>
          <span>只看有内容的评价</span>
        </div>
      </div>
      <ul class="ratings_con">
        <li v-for="rating in food.ratings" v-if="rating.rateType==type||type==2" v-show="rating.show">
          <div class="head">
            <span class="time">{{rating.rateTime|time}}</span>
            <span class="name">{{rating.username}} <img :src="rating.avatar" alt=""></span>
          </div>
          <div class="content">
            <i class="icon-thumb_up" v-if="rating.rateType==0"
               :class="{active:rating.attitude}" @click="rating.attitude=!rating.attitude"></i>
            <i class="icon-thumb_down" v-else=""
               :class="{active:rating.attitude}" @click="rating.attitude=!rating.attitude"></i>
            <span class="text">{{rating.text}}</span>
          </div>
        </li>
      </ul>
      <div class="return" @click="reback"><i class="icon-arrow_lift"></i></div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import Scroll from 'better-scroll';

  export default {
    props:['food'],
    data(){
      return{
        goodnum:null,
        badnum:null,
        detailsScroll:null,
        type:2,
        showAll:true
      }
    },
    created(){
      this.food.ratings.forEach(rating=>{
        Vue.set(rating,'show',true);
        Vue.set(rating,'attitude',false);
        if(rating.rateType==0){
          this.goodnum++;
        }else{
          this.badnum++;
        }
      });
      this.$nextTick(()=>{
        this.detailsScroll=new Scroll(this.$refs.details_scroll,{
          click: true,
          probeType: 3
        })
      });
    },
    methods:{
      changeShow(){
        if(this.showAll){
          this.food.ratings.forEach(rating=>{
            if(rating.text==''){
              rating.show=false;
            }
          });
          this.showAll=false;
        }else{
          this.food.ratings.forEach(rating=>{
            rating.show=true;
          });
          this.showAll=true;
        }
      },
      addCart(){
        if(!this.food.count){
          Vue.set(this.food,'count',1);
        }else{
          this.food.count++;
        }
      },
      reback(){
        this.$emit('close',null);
      }
    }
  }
  Vue.filter('time',(val)=>{
    let time0=new Date(val);
    let year=time0.getFullYear();
    let month=time0.getMonth()+1;
    if(month<10){
      month='0'+month;
    }
    let date=time0.getDate();
    if(date<10){
      date='0'+date;
    }
    let hour=time0.getHours();
    if(hour<10){
      hour='0'+hour;
    }
    let minute=time0.getMinutes();
    if(minute<10){
      minute='0'+minute;
    }
    return year+'-'+month+'-'+date+' '+hour+':'+minute
  })
</script>

<style>
  .details{
    position: fixed;
    top:0;
    left:0;
    width: 100%;
    height:100%;
    background: #f3f5f7;
    /*display: none;*/
  }
  .details .bigpic{
    width: 100%;
  }
  .details .bigpic img{
    width: 100%;
    height: 350px;
    display: block;
    background: lightblue;
  }
  .details .headinfo{
    padding: 18px;
    box-sizing: border-box;
    background: #fff;
    border-bottom:1px solid rgba(7,17,27,0.1);
    position: relative;
  }
  .details .headinfo .name{
    font-size: 14px;
    font-weight: 700;
    line-height: 14px;
    color: rgb(7,17,27);
  }
  .details .headinfo .sell_info{
    margin: 8px 0 18px;
    font-size: 10px;
    line-height: 10px;
  }
  .details .headinfo .sell_info span{
    color: rgb(147,153,159);
  }
  .details .headinfo .sell_info span:last-child{
    margin-left: 12px;
  }
  .details .headinfo .price{
    line-height: 24px;
  }
  .details .headinfo .price .new{
    font-size: 14px;
    font-weight: 700;
    color: rgb(240,20,20);
  }
  .details .headinfo .price .new span{
    font-size: 10px;
    font-weight: normal;
    color: rgb(240,20,20);
  }
  .details .headinfo .price .old{
    font-size: 10px;
    color: rgb(147,153,159);
    font-weight: 700;
  }
  .details .headinfo .price .old span{
    color: rgb(147,153,159);
    font-weight: normal;
  }
  .details .headinfo .addcart{
    width: 74px;
    height: 24px;
    background: rgb(0,160,220);
    border-radius: 12px;
    font-size: 10px;
    color: #fff;
    line-height: 24px;
    text-align: center;
    position: absolute;
    right: 18px;
    bottom: 18px;
  }
  .details .desprition{
    margin: 16px 0;
    padding: 18px;
    box-sizing: border-box;
    background: #fff;
    border-bottom:1px solid rgba(7,17,27,0.1);
    border-top: 1px solid rgba(7,17,27,0.1);
  }
  .details h4{
    font-size: 14px;
    color: rgb(7,17,27);
    font-weight: 400;
    line-height: 14px;
  }
  .details .desprition p{
    margin:6px 8px 0;
    font-size: 12px;
    color: rgb(77,85,93);
    font-weight: 200;
    line-height: 24px;
  }
  .details .details_ratings{
    background: #fff;
    border-top: 1px solid rgba(7,17,27,0.1);
    padding: 18px 18px 0;
    box-sizing: border-box;

    border-bottom:1px solid rgba(7,17,27,0.1);
  }
  .details .ratings_cls{
    padding: 18px 0;
    border-bottom: 1px solid rgba(7,17,27,0.1);
  }
  .details .ratings_cls div{
    border-radius: 2px;
    padding: 10px;
    float: left;
    margin-right: 10px;
    color: #4d555d;
    font-size: 14px;
  }
  .details .ratings_cls div.all{
    background: rgb(0,160,220);
  }
  .details .ratings_cls div.good{
    background: rgba(0,160,220,0.4);
  }
  .details .ratings_cls div.bad{
    background: #ccc;
  }
  .details .ratings_cls div.active{
    color: #fff;
  }
  .details .ratings_cls div span{
    font-size: 10px;
  }
  .details .btn{
    padding:16px 0;
    box-sizing: border-box;
    color: rgb(147,153,159);
    line-height: 22px;
  }
  .details .btn i{
    font-size: 20px;
    float: left;
    margin-right: 3px;
  }
  .details .btn.active i{
    color: rgb(0,160,220);
  }
  .details .btn span{
    font-size: 14px;
  }
  .details .ratings_con{
    background: #fff;
    padding: 0 16px 46px;
  }
  .details .ratings_con li{
    padding: 16px 0;
    border-bottom: 1px solid rgba(7,17,27,0.1);
  }
  .details .ratings_con li .head{
    font-size: 10px;
    line-height: 12px;
  }
  .details .ratings_con li .head .time{
    color: rgb(147,153,159);
  }
  .details .ratings_con li .head .name{
    color: rgb(147,153,159);
    float: right;
  }
  .details .ratings_con li .head .name img{
    width: 12px;
    height: 12px;
    border-radius: 50%;
    display: inline-block;
    float: right;
    background: lightblue;
    margin-left: 6px;
  }
  .details .ratings_con li .content{
    margin-top: 6px;
    font-size: 12px;
  }
  .details .ratings_con li .content i{
    color: rgb(147,153,159);
  }
  .details .ratings_con li .content i.active{
    color: rgb(0,160,220);
  }
  .details .ratings_con li .content span{
    margin-left: 4px;
    color: rgb(7,17,27);
    line-height: 16px;
  }
  .details .return{
    width:100%;
    height:30px;
    background: rgba(255,255,255,0.5);
    position: fixed;
    top: 0;
    left:0;
    text-align: left;
    line-height: 30px;
  }
  .details .return i{
    margin-left: 5px;
  }
</style>



