<template>
    <div class="control clearfix" v-if="food!=null">
      <div class="sub" v-show="food.count>0" @click="sub">_</div>
      <div class="num" v-show="food.count>0">{{food.count}}</div>
      <div class="add" @click="add">+</div>
    </div>
</template>

<script>
  import Vue from 'vue';
  export default {
    props:['food'],
    methods:{
      add(){
        if(!this.food.count){
          Vue.set(this.food,'count',1);
        }else{
          this.food.count++;
        }
      },
      sub(){
        this.food.count--;
      }
    }
  }
</script>

<style>

  .control .sub{
    width: 20px;
    height: 20px;
    box-sizing: border-box;
    border:2px solid rgb(0,160,220);
    border-radius: 50%;
    text-align: center;
    line-height: 2px;
    font-weight: 600;
    color: rgb(0,160,220);
    float: left;
  }
  .control .num{
    width: 20px;
    height: 20px;
    float: left;
    font-size: 10px;
    color: rgb(147,153,159);
    text-align: center;
    line-height: 20px;
  }
  .control .add{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    text-align: center;
    line-height: 22px;
    font-weight: 600;
    background: rgb(0,160,220);
    color: #fff;
    float: right;
  }
</style>



