<template>
  <transition name="fade">
    <div class="ratings" v-if="seller!=null&&ratings!=null" ref="ratings_scroll">
      <div>
      <div class="scores_wrapper">
        <div class="average_socre">
          <div class="score">{{seller.score}}</div>
          <div class="text">综合评分</div>
          <div class="rankRate">高于周边商家{{seller.rankRate}}%</div>
        </div>
        <div class="other_score">
          <div class="serviceScore clearfix">
            <div class="text">服务态度</div>
              <!--星级-->
              <star :score="seller.serviceScore"></star>
            <div class="num">{{seller.serviceScore}}</div>
          </div>
          <div class="serviceScore clearfix">
            <div class="text">食物评分</div>
              <!--星级-->
              <star :score="seller.foodScore"></star>
            <div class="num">{{seller.foodScore}}</div>
          </div>
          <div class="time clearfix">
            <div class="text">送达时间</div>
            <div class="minutes">{{seller.deliveryTime}}分钟</div>
          </div>
        </div>
      </div>
      <div class="ratings_wrapper">
          <div class="ratings_cls clearfix">
            <div class="all" :class="{active:type==2}" @click="type=2">全部 <span>{{ratings.length}}</span></div>
            <div class="good" :class="{active:type==0}" @click="type=0">满意 <span>{{goodnum}}</span></div>
            <div class="bad" :class="{active:type==1}" @click="type=1">不满意 <span>{{badnum}}</span></div>
          </div>
          <div class="btn" @click="changeShow" :class="{active:!showAll}">
            <i class="icon-check_circle"></i>
            <span>只看有内容的评价</span>
          </div>
          <ul>
            <li v-for="rating in ratings" v-if="rating.rateType==type||type==2" v-show="rating.show">
              <div class="icon">
                <img :src="rating.avatar" alt="">
              </div>
              <div class="info">
                <div class="name">{{rating.username}}<span class="time">{{rating.rateTime|time}}</span></div>
                <div class="score clearfix">
                  <!--评星-->
                  <star :score="rating.score"></star>
                  <span v-show="rating.deliveryTime" class="time">{{rating.deliveryTime}}分钟送达</span>
                </div>
                <div class="content">{{rating.text}}</div>
                <div class="others">
                  <i class="icon-thumb_up" v-if="rating.rateType==0"
                     :class="{active:rating.attitude}" @click="rating.attitude=!rating.attitude"></i>
                  <i class="icon-thumb_down" v-else=""
                     :class="{active:rating.attitude}" @click="rating.attitude=!rating.attitude"></i>
                  <span class="food" v-for="food in rating.recommend">{{food}}</span>
                </div>
              </div>
            </li>
          </ul>
      </div>
      <!--<shopcart ></shopcart>-->
      </div>
    </div>
  </transition>

</template>

<script>
//  import Shopcart from '../Shopcart.vue';
  import axios from 'axios';
  import Star from '../stars/Star.vue';
  import Vue from 'vue';
  import Scroll from 'better-scroll';
  export default {
    data(){
      return {
        seller:null,
        ratings:null,
        time:null,
        goodnum:null,
        badnum:null,
        ratingsScroll:null,
        type:2,
        showAll:true
      }
    },
    components:{
      Star
//      Shopcart,
    },
    created(){
      let that=this;
      axios.get('/api/seller')
        .then((res)=>{
//          console.log(res);
          that.seller=res.data.data;
        }).catch((err)=>{
        console.log(err);
      });
      axios.get('/api/ratings')
        .then((res)=>{
//          console.log(res);
          that.ratings=res.data.data;
          that.ratings.forEach(rating=>{
            Vue.set(rating,'show',true);
            Vue.set(rating,'attitude',false);
            if(rating.rateType==0){
              that.goodnum++;
            }else{
              that.badnum++;
            }
          });
          that.$nextTick(()=>{
            that.ratingsScroll=new Scroll(that.$refs.ratings_scroll,{
              click: true,
              probeType: 3
            })
          });
        }).catch((err)=>{
        console.log(err);
      });
    },
    methods:{
      changeShow(){
        if(this.showAll){
          this.ratings.forEach(rating=>{
            if(rating.text==''){
              rating.show=false;
            }
          });
          this.showAll=false;
        }else{
          this.ratings.forEach(rating=>{
            rating.show=true;
          });
          this.showAll=true;
        }
      }
    }
  }
  Vue.filter('time',(val)=>{
    let time0=new Date(val);
    let year=time0.getFullYear();
    let month=time0.getMonth()+1;
    if(month<10){
      month='0'+month;
    }
    let date=time0.getDate();
    if(date<10){
      date='0'+date;
    }
    let hour=time0.getHours();
    if(hour<10){
      hour='0'+hour;
    }
    let minute=time0.getMinutes();
    if(minute<10){
      minute='0'+minute;
    }
    return year+'-'+month+'-'+date+' '+hour+':'+minute
  }) //时间过滤器
//  console.log(new Date(1469261964000));
</script>

<style>
  .ratings{
    position: absolute;
    width: 100%;
    top: 175px;
    /*bottom: 46px;*/
    bottom: 0;
    overflow: hidden;
    background: #eee;
  }
  .scores_wrapper{
    width: 100%;
    display: flex;
    padding: 18px 0 ;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    border-bottom: 1px solid #ccc;
    background: #fff;
  }
  .scores_wrapper .average_socre{
    flex:1;
    text-align: center;
  }
  .scores_wrapper .average_socre .score{
    font-size: 24px;
    color:rgb(255,153,0);
    line-height: 28px;
  }
  .scores_wrapper .average_socre .text{
    font-size: 12px;
    color: rgb(7,17,27);
    line-height: 12px;
    margin:6px 0 8px;
  }
  .scores_wrapper .average_socre .rankRate{
    font-size: 10px;
    color: rgb(147,153,159);
    line-height: 10px;
    margin-bottom: 6px;
  }
  .scores_wrapper .other_score{
    flex: 0 0 237px;
    border-left: 1px solid rgba(7,17,27,0.1);
    padding:0 24px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    line-height: 18px;

  }
  .scores_wrapper .other_score .serviceScore{
    margin-bottom: 8px;
  }
  .scores_wrapper .other_score .serviceScore .text{
    font-size: 12px;
    color: rgb(7,17,27);
    line-height: 18px;
    float: left;
  }
  .scores_wrapper .other_score .serviceScore .star{
    float: left;
    margin:2px 12px 0;
  }
  .scores_wrapper .other_score .serviceScore .star .star-item{
    width: 14px;
    height: 14px;
    background-size:14px 14px;
    margin-right: 4px;
    margin-left: 0;
  }
  .scores_wrapper .other_score .serviceScore .num{
    float: left;
    font-size: 12px;
    color: rgb(255,153,0);
    line-height: 18px;
  }
  .scores_wrapper .other_score .time .text{
    float: left;
    font-size: 12px;
    color: rgb(7,17,27);
  }
  .scores_wrapper .other_score .time .minutes{
    float: left;
    margin-left: 12px;
    font-size: 12px;
    color: rgb(147,153,159);
  }
  .ratings_wrapper{
    margin-top: 18px;
    background: white;
    border-top: 1px solid #ccc;
  }
  .ratings_wrapper .ratings_cls{
    margin:0 18px;
    padding: 18px 0;
    box-sizing: border-box;
    border-bottom:1px solid rgba(7,17,27,0.1);
  }
  .ratings_wrapper .ratings_cls div{
    border-radius: 2px;
    padding: 10px;
    float: left;
    margin-right: 10px;
    color: #4d555d;
    font-size: 14px;
  }
  .ratings_wrapper .ratings_cls div.all{
    background: rgb(0,160,220);
  }
  .ratings_wrapper .ratings_cls div.good{
    background: rgba(0,160,220,0.4);
  }
  .ratings_wrapper .ratings_cls div.bad{
    background: #ccc;
  }
  .ratings_wrapper .ratings_cls div.active{
    color: #fff;
  }
  .ratings_wrapper .ratings_cls div span{
    font-size: 10px;
  }
  .ratings_wrapper .btn{
    padding:16px;
    box-sizing: border-box;
    border-bottom: 1px solid rgba(7,17,27,0.2);
    color: rgb(147,153,159);
    line-height: 22px;
  }
  .ratings_wrapper .btn i{
    font-size: 20px;
    float: left;
    margin-right: 3px;
  }
  .ratings_wrapper .btn.active i{
    color: rgb(0,160,220);
  }
  .ratings_wrapper .btn span{
    font-size: 14px;

  }
  .ratings_wrapper ul{
    padding: 0 18px;
  }
  .ratings_wrapper ul li {
    padding:18px 0;
    border-bottom: 1px solid rgba(7,17,27,0.1);
    display: flex;
  }
  .ratings_wrapper ul li .icon{
    flex:0 0 28px;
    width: 28px;
    height: 28px;
    margin-right: 12px;
    background: lightblue;
  }
  .ratings_wrapper ul li .icon img{
    width: 100%;
    height:100%;
    border-radius: 50%;
  }
  .ratings_wrapper ul li .info{
    flex:1;
  }
  .ratings_wrapper ul li .info .name{
    font-size: 10px;
    line-height: 12px;
    color: rgb(7,17,27);
  }
  .ratings_wrapper ul li .info .name .time{
    float: right;
    color:rgb(147,153,159);
  }
  .ratings_wrapper ul li .info .score{
    margin:4px 0 6px;
  }
  /*star组件样式*/
  .ratings_wrapper ul li .info .score .star{
    float: left;
    margin-top: 2px;
  }
  .ratings_wrapper ul li .info .score .star .star-item{
    width: 12px;
    height: 12px;
    background-size:12px 12px;
    margin-right: 2px;
  }
  .ratings_wrapper ul li .info .score .time{
    margin-left: 6px;
    font-size: 10px;
    line-height: 12px;
    color: rgb(7,17,27);
  }
  .ratings_wrapper ul li .info .content{
    font-size: 12px;
    line-height: 18px;
    color: rgb(7,17,27);
  }
  .ratings_wrapper ul li .info .others{
    margin-top: 8px;
  }
  .ratings_wrapper ul li .info .others i{
    font-size: 16px;
    line-height: 18px;
    color: rgb(183,187,191);
    float: left;
  }
  .ratings_wrapper ul li .info .others i.active{
    color: rgb(0,160,220);
  }
  .ratings_wrapper ul li .info .others span{
    border:1px solid rgba(7,17,27,0.1);
    margin-left: 8px;
    border-radius: 2px;
    font-size: 9px;
    color: rgb(147,153,159);
    line-height: 16px;
    padding:0 6px;
  }
</style>



