<template>
  <transition name="fade">
  <div class="seller" ref="seller_Swipper" v-if="seller!=null">
      <div>
        <div class="head">
          <div class="base">
            <h2>{{seller.name}}</h2>
            <div class="score">
              <!--星级-->
              <star :score="seller.score"></star>
              <span>({{parseInt(seller.score*0.2*seller.sellCount)}})</span>
              <span>月售{{seller.sellCount}}单</span>
            </div>
            <div class="collect" @click="ifCollect=!ifCollect">
              <div>
                <i class="icon-favorite" :class="{active:ifCollect}"></i>
              </div>
                <span v-if="!ifCollect">收藏</span>
                <span v-else="">已收藏</span>
            </div>
          </div>
          <div class="other">
            <ul>
              <li>
                <p class="text">起送价</p>
                <p class="con">
                  {{seller.minPrice}}<span>元</span>
                </p>
              </li>
              <li>
                <p class="text">商家配送</p>
                <p class="con">
                  {{seller.deliveryPrice}}<span>元</span>
                </p>
              </li>
              <li>
                <p class="text">平均配送时间</p>
                <p class="con">
                  {{seller.deliveryTime}}<span>分钟</span>
                </p>
              </li>
            </ul>
          </div>
        </div>
        <div class="notice">
          <h2>公告和活动</h2>
          <div class="text">{{seller.bulletin}}</div>
          <ul>
            <li v-for="support in seller.supports">
              <span class="icon" :class="supportPic[support.type]"></span>
              {{support.description}}
            </li>
          </ul>
        </div>
        <div class="photos">
          <h2>商家实景</h2>
          <div class="picBox" ref="picBox">
            <div class="clearfix">
              <img v-for="pic in seller.pics" :src="pic" alt="">
            </div>
          </div>
        </div>
        <div class="info">
          <h2>商家信息</h2>
          <ul>
            <li v-for="info in seller.infos">{{info}}</li>
          </ul>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import axios from 'axios';
  import BScroll from 'better-scroll';
  import Star from '../stars/Star.vue';
  export default {
    data(){
      return{
        seller:null,
        sellerScroll:null,
        supportPic:null,
        ifCollect:false,
        picsScroll:null
      }
    },
    components:{
      Star
    },
    created(){
      this.supportPic = ['decrease', 'discount', 'guarantee', 'invoice', 'special'];
      let that=this;
      axios.get('/api/seller')
        .then(res=>{
          that.seller=res.data.data;
          that.$nextTick(()=>{
            that.sellerScroll=new BScroll(that.$refs.seller_Swipper,{
              click:true
            });
            that.picsScroll=new BScroll(that.$refs.picBox,{
              click:true,
              scrollX: true
            });
          });
        }).catch(err=>{
          console.log(err);
      });
    }
  }
</script>

<style>
  .seller{
    position: absolute;
    width: 100%;
    top: 175px;
    /*bottom: 46px;*/
    bottom: 0;
    overflow: hidden;
    background: #eee;
  }
  .seller .head{
    padding: 18px;
    box-sizing: border-box;
    border-bottom: 1px solid #ccc;
    background: #fff;
  }
  .seller .head .base{
    border-bottom: 1px solid rgba(7,17,27,0.1);
    position: relative;
  }
  .seller h2{
    font-size: 14px;
    color: rgb(7,17,27);
    line-height: 14px;
  }
  .seller .head .base .score{
    margin: 8px 0 18px;
  }
  .seller .head .base .score .star{
    float: left;
  }
  .seller .head .base .score .star .star-item{
    width: 18px;
    height: 18px;
    background-size:18px;
    margin:0 4px 0 0;
  }
  .seller .head .base .score span{
    font-size: 10px;
    color: rgb(77,85,93);
    line-height: 18px;
  }
  .seller .head .base .score span:first-child{
    margin:0 12px 0 8px;
  }
  .seller .head .base .collect{
    position: absolute;
    top: 5px;
    right: 5px;
    text-align: center;
  }
  .seller .head .base .collect i{
    font-size: 24px;
    color: rgb(183,187,191);
    line-height: 24px;
  }
  .seller .head .base .collect i.active{
    color: rgb(240,20,20);
  }
  .seller .head .base .collect span{
    font-size: 10px;
    color: rgb(77,85,93);
    line-height: 10px;
    margin-top: 4px;
  }
  .seller .head .other{
    margin: 18px 0 0;
  }
  .seller .head .other ul{
    display: flex;
  }
  .seller .head .other ul li{
    flex:1;
    text-align: center;
    border-right: 1px solid rgba(7,17,27,0.1);
  }
  .seller .head .other ul li:last-child{
    border:none;
  }
  .seller .head .other ul li .text{
    font-size: 10px;
    color: rgb(147,153,159);
    line-height: 10px;
  }
  .seller .head .other ul li .con{
    margin-top: 4px;
    font-size: 24px;
    font-weight: 200;
    color: rgb(7,17,27);
    line-height: 24px;
  }
  .seller .head .other ul li .con span{
    font-size: 10px;
    line-height: 10px;
  }
  .seller .notice{
    margin-top: 16px;
    border-top: 1px solid #ccc;
    padding: 18px 18px 0;
    box-sizing: border-box;
    background: #fff;
  }
  .seller .notice .text{
    margin:8px 12px 16px;
    font-size: 12px;
    color: rgb(240,20,20);
    line-height: 24px;
  }
  .seller .notice ul li{
    border-top: 1px solid rgba(7,17,27,0.1);
    padding:16px 12px;
    box-sizing: border-box;
    font-size: 12px;
    color: rgb(7,17,27);
    line-height: 16px;
  }
  .seller .notice ul li .icon{
    width: 16px;
    height: 16px;
    display: inline-block;
    float: left;
    background: lightblue;
    -webkit-background-size: 16px;
    background-size: 16px;
    margin-right: 6px;
  }
  .seller .notice ul li .icon.decrease {
    background-image: url('images/decrease_3@2x.png');
  }
  .seller .notice ul li .icon.discount {
    background-image: url('images/discount_3@2x.png');
  }
  .seller .notice ul li .icon.guarantee {
    background-image: url('images/guarantee_3@2x.png');
  }
  .seller .notice ul li .icon.invoice {
    background-image: url('images/invoice_3@2x.png');
  }
  .seller .notice ul li .icon.special {
    background-image: url('images/special_3@2x.png');
  }
  .seller .photos{
    margin: 16px 0;
    background: #fff;
    padding: 18px;
    box-sizing: border-box;
  }
  .seller .photos .picBox{
    width:100%;
    margin-top: 12px;
    overflow: hidden;
  }
  .seller .photos .picBox div{
    width:150%;
    white-space: nowrap;
  }
  .seller .photos .picBox img{
    width: 120px;
    height: 90px;
    display:block;
    float: left;
    background: lightblue;
    margin-right: 6px;
  }
  .seller .info{
    background: #fff;
    padding: 18px;
    box-sizing: border-box;
  }
  .seller .info ul{
    margin-top: 12px;
  }
  .seller .info ul li{
    border-top: 1px solid rgba(7,17,27,0.1);
    padding: 16px 12px;
    font-size: 12px;
    color: rgb(7,17,27);
    line-height: 16px;
  }
</style>



